// position

@mixin posa-tl($t: 0, $l:0) {
  position: absolute;
  top: $t;
  left: $l;
}

@mixin posa-tr($t: 0, $r:0) {
  position: absolute;
  top: $t;
  right: $r;
}

@mixin posa-bl($b: 0, $l:0) {
  position: absolute;
  bottom: $b;
  left: $l;
}

@mixin posa-br($b: 0, $r:0) {
  position: absolute;
  bottom: $b;
  right: $r;
}

@mixin posf-t($t: 0, $l:0) {
  position: fixed;
  top: $t;
  left: $l;
}

@mixin posf-b($b: 0, $l:0) {
  position: fixed;
  bottom: $b;
  left: $l;
}

@mixin clear-fix() {
  display: block;
  clear: both;
  content: '';
}

@mixin center-block() {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

// border

@mixin bdrs($z: 5px) {
  -webkit-border-radius: $z;
  -moz-border-radius: $z;
  border-radius: $z;
}

@mixin bdt($t: 1px, $c: #000) {
  border-top: $t solid $c;
}

@mixin bdb($t: 1px, $c: #000) {
  border-bottom: $t solid $c;
}
@mixin bdl($t: 1px, $c: #000) {
  border-left: $t solid $c;
}

@mixin bdr($t: 1px, $c: #000) {
  border-right: $t solid $c;
}

@mixin bd($t: 1px, $c: #000) {
  border: $t solid $c;
}


// flex direction gap
@mixin flex-col($g: 0) {
  display: flex;
  flex-direction: column;
  gap: $g;
}

